import React from 'react';
import {Jumbotron, Container} from "reactstrap";
//import profile from '../profile.json';

class ProfileSummary extends React.Component {
  render() {
    return <Jumbotron className="summary">
      <Container>
        <h2 className="display-3">Full Stack Developer</h2>
        <p lassName="lead"> <strong>I am very passionate about software development. Getting things work effectively and efficiently for clients. Always growing as a person. Looking forward to give my best to my team and co-workers.</strong></p>
      </Container>
    </Jumbotron>;
  }
}

export default ProfileSummary;